<template>
  <div>
    <!--面包屑导航-->
    <div class="breadcrumb">
      <div class="container">
        <h2>
          <a href>首页</a>&gt;
          <a href>产品中心</a>
        </h2>
      </div>
    </div>
    <!--页面主体-->
    <div class="main container">
      <div class="pl_header" v-if="data">
        <router-link to="/product/1" :class="{ cur: type == 1 }">净美仕净化器</router-link>
        <router-link to="/product/2" :class="{ cur: type == 2 }">净美仕滤网</router-link>
      </div>
      <!-- 产品列表-->
      <ul class="product_list clearfloat" v-if="data">
        <li v-for="(item, index) in data.data" :key="index">
          <router-link :to="{name:'ProductDetail',params:{pid:item.pid}}">
            <img :src="url + item.pic" />
          </router-link>
          <div class="pdlist_text clearfloat">
            <h3>
              <p>{{ item.model }}</p>
              <span>{{ item.title2 }}</span>
            </h3>
            <router-link :to="{name:'ProductDetail',params:{pid:item.pid}}">查看详情</router-link>
          </div>
        </li>
      </ul>
      <!-- 分页导航-->
      <div class="pages" v-if="data">
        <a @click="getData(data.pageNum-1)" v-if="data.pageNum > 1">上一页</a>
        <a v-else class="default">上一页</a>
        <a
          v-for="(item, index) in data.pageCount"
          :key="index"
          @click="getData(item)"
          :class="{cur:item == data.pageNum}"
        >{{ item }}</a>
        <a @click="getData(data.pageNum + 1)" v-if="data.pageNum < data.pageCount" >下一页</a>
        <a v-else class="default">下一页</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["type"],
  data() {
    return {
      data: null,
      url: "http://101.96.128.94:9999/mfresh/",
    };
  },
  beforeRouteUpdate (to, from, next) {
    // 监听路由变化
    // 当路由发生变化时 更新数据
    this.getData(1);
    next();
  },
  methods: {
    getData(pageNum) {
      this.axios
        .get(`product_select.php?type=${this.type}&pageNum=${pageNum}`)
        .then((res) => {
          // console.log(res)
          this.data = res.data;
        })
        .catch((err) => {
          console.error(err);
        });
    },
  },
  mounted() {
    this.getData(1);
  },
};
</script>

<style>
.pages {
  -moz-user-select: none; /*火狐*/
  -webkit-user-select: none; /*webkit浏览器*/
  -ms-user-select: none; /*IE10*/
  -khtml-user-select: none; /*早期浏览器*/
  user-select: none;
}

.pages > a {
  cursor: pointer;
}
</style>
